  font-size: 3em;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 32px);
  height: inherit;
  outline: 1px solid rgba(179, 255, 0, 0.459);
  background: rgba(114, 114, 114, 0.5);
}

body {
  transition: all ease 1s;
}

#menu {
  width: 300px;
  margin-left: -245px;
  background: #fbf0ff;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  transition: all ease 1s;
  z-index: 9999;
  opacity: 0.8;
}

#menu .c-hamburger {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 55px;
  height: 55px;
  font-size: 0;
  cursor: pointer;
  border: 0;
}

#menu .c-hamburger:focus {
  outline: 0;
}

#menu .c-hamburger span {
  display: block;
  position: absolute;
  top: 25px;
  left: 18px;
  right: 8px;
  height: 3px;
  background: white;
}

#menu .c-hamburger span::before {
  position: absolute;
  display: block;
  left: 0;
  top: -10px;
  width: 100%;
  height: 3px;
  background: white;
  content: "";
}

#menu .c-hamburger span::after {
  position: absolute;
  display: block;
  left: 0;
  bottom: -10px;
  width: 100%;
  height: 3px;
  background: white;
  content: "";
}

#menu .c-hamburger-line {
  background: #28cc59e3;
  transition: background 0.5s;
}

#menu .c-hamburger-line span {
  transition: background 0s 0.3s;
}

#menu .c-hamburger-line span::before {
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s;
  transition-property: top, transform;
}

#menu .c-hamburger-line span::after {
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s;
  transition-property: bottom, transform;
}

#menu .is-active {
  background: red;
}

#menu .is-active span {
  background: none;
}

#menu .is-active span::before {
  top: 0;
  transform: rotate(-45deg);
  transition-delay: 0s, 0.1s;
}

#menu .is-active span::after {
  bottom: 0;
  transform: rotate(45deg);
  transition-delay: 0s, 0.1s;
}

#menu .content-inside {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px 10px 10px 10px;
  opacity: 0;
  transition-delay: 0.3s;
  transition: ease 2s;
}

#menu .content-inside a {
  font-size: 3em;
  text-decoration: none;
  transition: 0.5s;
}

#menu .content-inside a:hover {
  transition: 0.5s;
  text-decoration: underline;
}

#menu .content-show {
  opacity: 1;
  transition-delay: 0.3s;
}

#menu.nav-active {
  margin-left: 0;
}

